import React from 'react'
import Container from './container'
import Executor from './executor'

export default function createModel(hook, context) {
    const container = new Container()
    const div = document.createElement('div')
    React.render(
        <Executor
            onUpdate={v => {
                container.data = v
                container.notify()
            }}
            hook={() => hook()}
        />, div
    )

    const useModel = selector => {
        const [state, setState] = useState(() => selector(container.data))
        const stateRef = useRef(state)
        const subscriber = (val) => {
            const oldState = stateRef.current
            const newState = selector(val)
            if (!compare(oldState, newState)) {
                setState(selector(val))
            }
            stateRef.current = newState
        }
        container.subscribers.add(subscriber)

        // 组件卸载时移除订阅者
        useEffect(() => {
            container.subscribers.delete(subscriber)
        }, [subscriber])

        return state
    }
    return useModel
}